<template>
  <div class="d-flex flex-column vh-100">
    <div class="page py-4" style="overflow-x:hidden;">
      <div class="iphoneframe-container" ref="iphone">
        <router-link to="/account">
          <BackButton />
        </router-link>
        <transition name="fade-top">
          <PreviewCardWithIphoneFrame
            class="w-100"
            :image="previewCardImage"
            :title="previewCardTitle"
            :titleMyan="previewCardTitleMyan"
            :desc="previewCardDesc"
            :descMyan="previewCardDescMyan"
            :isFindOther="true"
            :isContactUs="true"
            type="promote"
            firstButtonText="Not happy"
            secondButtonText="Just Ok"
            thirdButtonText="Very happy"
            v-show="showPreview"
          />
        </transition>
        <div class="mt-4 w-100 d-flex justify-content-center" v-if="showPreview">
          <button
            class="btn btn-circle"
            style="width: 40px; height: 40px;background:#B7B7B7;"
            @click="showPreview = false"
          >
            <CloseIcon class="close-icon" />
          </button>
        </div>

        <div class="mt-4 w-100 title">
          <StageTitle stageNum="7" :title="stageInfoText.title" :desc="stageInfoText.desc" />
        </div>
        <div class="mt-4 w-100 p-3">
          <AddedForm
            v-model="promoteInfo"
            :info="info"
            type="promote"
            @add="addPromoteProduct"
            @uploadPhoto="uploadPhoto"
          />
        </div>
        <div class="w-100 d-flex justify-content-center">
          <button class="btn btn-primary" @click="Preview()">
            <i class="las la-eye"></i>
            Preview
          </button>
        </div>
        <div class="mt-2 w-100 py-4 px-5">
          <MobileToggle
            :title="toggles.askCustomerInfomation.title"
            :desc="toggles.askCustomerInfomation.desc"
            v-model="isEnabledAskCustomerInfo"
            @change="addShopNature"
          />
        </div>
        <div class="mt-2 w-100 d-flex flex-center">
          <router-link to="/account/promote">
            <ForwardButton @click.native="$emit('showPage',1)" />
          </router-link>
        </div>
      </div>
    </div>
    <bottom-navigation-bar />
    <Loading v-if="showLoading" />
    <AlertBox v-if="!shopExist" :alertInfo="alertInfo" @goto="route('/account/contactinfo')" />
  </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import ForwardButton from "@/components/ForwardButton";
import CloseIcon from "@/assets/icons/Close";
import StageTitle from "@/components/StageTitle";
import MobileToggle from "@/components/MobileToggle";
import AddedForm from "@/components/AddedForm";
import axios from "axios";
import BottomNavigationBar from "@/components/BottomNavigationBar";
import { debounce } from "lodash";
import AlertBox from "@/components/AlertBox";
import Loading from "@/components/Loading";

export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    MobileToggle,
    BackButton,
    ForwardButton,
    AddedForm,
    CloseIcon,
    Loading,
    BottomNavigationBar,
    AlertBox
  },
  data() {
    return {
      showLoading: true,
      showPreview: false,
      pageActive: true,
      isEnabledAskCustomerInfo: false,
      photoUploading: false,
      promoteID:"",
      previewCardDefaults: {
        title: "Your promote product is awesome",
        desc: "Let's us know your promote on our product.We do appreciate it",
        image:
          "https://portalvhdslvb28rs1c3tmc.blob.core.windows.net/yammo/allstaricon/picture.png"
      },
      stageInfoText: {
        title: "'Promote' product",
        desc: "Fill your promote information once"
      },
      toggles: {
        askCustomerInfomation: {
          title: "Ask Customer Information",
          desc:
            "Enable this option will ask the customer name, contact number and address."
        }
      },
      promoteInfo: {
        title: "",
        desc: "",
        image: "",
        titleMyan: "",
        descMyan: ""
      },
      info: {
        title: "Type 'Promote information' title (Eng)",
        titleMyan: "Type 'Promote information' title (Myan)",
        desc: "Type 'Promote information' message (Eng)",
        descMyan: "Type 'Promote information' message (Myan)",
        photoText: "add 'Promote information' image",
        type: "PromoteInformation",
        buttons: [
          {
            title: "Track my order"
          },
          {
            title: "Find related more"
          },
          {
            title: "Find other"
          }
        ]
      },
      alertInfo: {
        title: "Alert",
        msg:
          "To fill your promote product information,you need to add contact information first.",
        buttons: [
          {
            text: "Add Shop",
            action: "goto"
          }
        ]
      } 
    };
  },
  methods: {
    addPromoteProduct: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.promoteID,
        Title: this.promoteInfo.title,
        Message: this.promoteInfo.desc,
        ShopGuid: this.shopID
      };
      if (this.promoteID == "") {
        await axios.post("promote/upsert", obj).then(res => {
          this.promoteID=res.data;
        });
      } else {
        await axios.put("promote/upsert", obj);
      }
    }, 3000),

    async uploadPhoto(url) {
      let obj = {
        Guid: this.promoteID,
        Photo: url,
        ShopGuid: this.shopID
      };
      this.photoUploading = true;
      if (this.promoteID == "") {
        await axios.post("promote/uploadphoto", obj).then(res => {
          this.photoUploading = false;
          this.promoteID=res.data;
        });
      } else {
        await axios.put("promote/uploadphoto", obj).then(() => {
          this.photoUploading = false;
        });
      }
    },

    async getPromote() {
      if (this.shopID == "" || this.shopID == "0") return;
      await axios
        .get(
          `promote?shopGuid=${this.shopID}&customerID=${this.customerID}`
        )
        .then(res => {
          if (res.data != null) {
            this.promoteInfo.title = res.data.tPromote.Title;
            this.promoteInfo.image = res.data.tPromote.PhotoUrl;
            this.promoteInfo.desc = res.data.tPromote.Message;
            this.promoteID=res.data.tPromote.Guid;
            this.isEnabledAskCustomerInfo = res.data.isEnabledAskCustInfo;
          }
          this.showLoading = false;
        });
    },

    addShopNature: debounce(async function() {
      let validate = this.validateForm();
      if (!validate) return;
      let obj = {
        Guid: this.shopID,
        CustomerID: this.customerID,
        IsEnabledCustomerInfoForPromoteItem: this.isEnabledAskCustomerInfo
      };
      await axios.put("shop/shopNatureFromStepSeven", obj);
    }, 3000),
    validateForm() {
      if (
        this.promoteInfo.title == "" &&
        this.promoteInfo.desc == "" &&
        this.promoteInfo.image == ""
      ) {
        return false;
      }
      return true;
    },
    Preview() {
      this.$refs.iphone.scrollIntoView();
      this.showPreview = true;
    }
  },
  computed: {
    previewCardTitle() {
      return this.promoteInfo.title
        ? this.promoteInfo.title
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.promoteInfo.desc
        ? this.promoteInfo.desc
        : this.previewCardDefaults.desc;
    },
    previewCardTitleMyan() {
      return this.promoteInfo.titleMyan
        ? this.promoteInfo.titleMyan
        : this.previewCardDefaults.title;
    },
    previewCardDescMyan() {
      return this.promoteInfo.descMyan
        ? this.promoteInfo.descMyan
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.promoteInfo.image
        ? this.promoteInfo.image
        : this.previewCardDefaults.image;
    },
    shopID() {
      return this.$store.state.shopID;
    },
    customerID() {
      return this.$store.state.customerID;
    },
    shopExist() {
      return this.shopID != "0";
    },
  },
  mounted() {
    this.getPromote();
  },
  watch:{
    shopID(){
      this.getPromote();
    }
  },
};
</script>
<style lang="scss">
@import "@/scss/_PageSetup.scss";
@import "@/scss/_Utilities.scss";
.page {
  flex: 1;
  overflow-y: auto;
}
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -1.5rem;
}
</style>
<style lang="scss" scoped>
@import "@/scss/_variables";
.title {
  padding-left: 0.8rem;
}
.btn-primary {
  border-radius: 40px;
  background-color: $lightBlue;
  border-color: unset;
  &:hover {
    border-color: unset;
    box-shadow: none;
  }
  &:active {
    border-color: unset;
    box-shadow: none;
  }
  &:focus {
    border-color: unset;
    box-shadow: none;
  }
}
.btn-circle {
  width: 40px;
  height: 40px;
  background: $grayActive;
}
.close-icon {
  width: 0.8rem;
}
</style>
